.navbar {
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
  }
  
  .navbar-logo {
    color: #fff;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
  }
  
  .fa-firstdraft {
    margin-left: 0.5rem;
    font-size: 1.6rem;
  }
  
  .nav-menu {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 1vw;
    list-style: none;
    text-align: center;
    width:auto;
    max-width: 100%;
    /* width: 70vw; */
    justify-content: end;
    padding-right: 10vw;
    /* margin-right: 5vw; */
    /* margin-left: 1rem; */
  }
  
  .nav-item {
    display: flex;
    align-items: center;
    height: 80px;
    cursor: pointer;
  }
  .nav-subitem {
    display: flex;
    align-items: center;
    height: 100%;
    cursor: pointer;
  }
  .nav-links {
    color:#006936;
    font-family: 'arial';
    font-weight: 700;
    font-size: 18px;
    text-decoration: none;
    padding: 0.5rem 3rem;
  }
  
  .nav-links:hover {
    /* background-color: #dbe2df; */
    border-radius: 4px;
    transition: all 0.2s ease-out;
    transition-delay: 1s;
  }
  
  .fa-bars {
    color: #fff;
  }
  
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
  }

  @media screen and (max-width: 1200px) {
    .nav-menu {
      display: grid;
      grid-template-columns: repeat(5, auto);
      grid-gap: 0px;
      list-style: none;
      text-align: center;
      max-width: 100%;
      width: auto;
      justify-content: end;
      margin-right: -1rem;
    }
    .nav-links {
      color:#006936;
      font-family: 'arial';
      font-weight: 700;
      font-size: 16px;
      text-decoration: none;
      padding: 0.5rem 2rem;
    }
  }
  
  @media screen and (max-width: 960px) {
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      max-width: 100%;
      width: auto;
      height: 90vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }
  
    .nav-menu.active {
      background: #242222;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
    }
  
    .nav-links:hover {
      background-color: #1888ff;
      border-radius: 0;
    }
  
    .navbar-logo {
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(25%, 50%);
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #fff;
      font-size: 2rem;
    }
  
    .nav-links-mobile {
      display: block;
      text-align: center;
      padding: 1.5rem;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      background: #1888ff;
      text-decoration: none;
      color: #fff;
      font-size: 1.5rem;
    }
  
    .nav-links-mobile:hover {
      background: #fff;
      color: #1888ff;
      transition: 250ms;
    }
  
    button {
      display: none;
    }
  }
  